import { Injectable } from "@angular/core";
import { AngularFirestore, AngularFirestoreCollection } from '@angular/fire/firestore';
import { BehaviorSubject, from, observable, Observable } from 'rxjs';
import { map, take } from "rxjs/operators";
import { Account } from "src/app/model/account";
import { Education } from "src/app/model/education";
import { ResponseEnity } from "src/app/model/response-entity";
import { Skill } from "src/app/model/skill";
import { WorkExperience } from "src/app/model/work-experience";
import { MappingService } from "../mapping.service";
import { AngularFireAuth } from "@angular/fire/auth";
import { AccountLogin } from "src/app/model/acccount-login";
import { JwttokenService } from "../jwttoken.service";

@Injectable({
    providedIn: "root"
})
export class AccountFirebaseService {
    private dbAccountPath = '/accounts';
    private dbEducationPath = '/educations';
    private dbSkillPath = '/skills';
    private dbWorkExperiencePath = '/work_experiences';
    private username = 'duongnh';

    private currentAccountSubject: BehaviorSubject<AccountLogin>;

    constructor(
        private firestore: AngularFirestore,
        private fireauth: AngularFireAuth,
        private mappingService: MappingService
    ) {
        this.currentAccountSubject = new BehaviorSubject<AccountLogin>(null);
        this.fireauth.onAuthStateChanged((user) => {
            if (user) {
                console.log(`user login ${user.displayName}`)
            } else {
            }
        })

        this.fireauth.credential.subscribe
    }

    public get getCurrentAccountValue(): AccountLogin {
        return this.currentAccountSubject.value;
    }

    getAccountByUsername(): Observable<Account> {
        let educationOfUserData = [];
        let skillOfUserData = [];
        let workExperienceOfUserData = [];
        const user: AngularFirestoreCollection<Account> = this.firestore.collection(
            this.dbAccountPath,
            ref => ref.where('username', '==', this.username)
        );
        this.getAdditonalInformationOfUser<Education>(this.dbEducationPath).subscribe(data => educationOfUserData = data)
        this.getAdditonalInformationOfUser<Skill>(this.dbSkillPath).subscribe(data => skillOfUserData = data)
        this.getAdditonalInformationOfUser<WorkExperience>(this.dbWorkExperiencePath).subscribe(data => workExperienceOfUserData = data)

        return user.snapshotChanges().pipe(
            map(changes =>
                changes.map(c => {
                    const account = {
                        ...c.payload.doc.data(),
                        id: c.payload.doc.id,
                        educationDTOs: educationOfUserData,
                        skillDTOs: skillOfUserData,
                        workExperienceDTOs: workExperienceOfUserData,
                    }

                    return this.mappingService.mappingAccount(account);
                })[0]
            )
        );
    }

    getEducationAccountByUsername(): Observable<Education[]> {
        return this.getAdditonalInformationOfUser<Education>(this.dbEducationPath).pipe(
            map(changes => this.mappingService.mappingEducationArray(changes))
        )
    }

    getSkillAccountByUsername(): Observable<Skill[]> {
        return this.getAdditonalInformationOfUser<Skill>(this.dbSkillPath).pipe(
            map(changes => this.mappingService.mappingSkillArray(changes))
        )
    }

    getWorkExperienceAccountByUsername(): Observable<WorkExperience[]> {
        return this.getAdditonalInformationOfUser<WorkExperience>(this.dbWorkExperiencePath).pipe(
            map(changes => this.mappingService.mappingWorkExperienceArray(changes))
        )
    }

    getLinkSocialNetworkById(): Observable<Account> {
        const user: AngularFirestoreCollection<Account> = this.firestore.collection(
            this.dbAccountPath,
            ref => ref.where('username', '==', this.username)
        );
        return user.snapshotChanges().pipe(
            map(changes =>
                changes.map(c => {
                    const account = {
                        ...c.payload.doc.data(),
                        id: c.payload.doc.id
                    }

                    return this.mappingService.mappingAccount(account);
                })[0]
            )
        );
    }

    updateByUsername(account: Account) {
        const promiseUpdateInfo: Promise<void>[] = [];

        // update user
        const accountUpdate = {
            ...this.mappingService.mappingAccountToFirebase(account)
        }
        const user: AngularFirestoreCollection<Account> = this.firestore.collection(
            this.dbAccountPath,
            ref => ref.where('username', '==', this.username)
        );
        user.get().subscribe((datas) => {
            const id = datas.docs[0].id
            promiseUpdateInfo.push(
                this.firestore.doc(`${this.dbAccountPath}/${id}`).update(accountUpdate)
            )
        })

        // update education
        const educationOfUser: AngularFirestoreCollection<Account> = this.firestore.collection(
            this.dbEducationPath,
            ref => ref.where('username', '==', this.username)
        );
        educationOfUser.get().subscribe((educationDatas) => {
            account.educationDTOs.forEach(eduUpdate => {
                const eduMapping = {
                    ...this.mappingService.mappingEducationToFirebase(eduUpdate),
                    username: this.username
                }

                if (eduUpdate.id) {
                    // update or delete
                    const docUpdate = educationDatas.docs.find(doc => doc.id == eduUpdate.id);

                    if (docUpdate) {
                        // update edu
                        promiseUpdateInfo.push(
                            this.firestore.doc(`${this.dbEducationPath}/${docUpdate.id}`).update(eduMapping)
                        )
                    }
                } else {
                    // add new edu
                    promiseUpdateInfo.push(
                        this.firestore.collection(`${this.dbEducationPath}`).add(eduMapping).then()
                    )
                }

            })

            const eduIds = account.educationDTOs.map(edu => edu.id)
            educationDatas.docs.filter(doc => !eduIds.includes(doc.id)).forEach(docDelete => {
                // delete
                promiseUpdateInfo.push(
                    this.firestore.doc(`${this.dbEducationPath}/${docDelete.id}`).delete()
                )
            })
        })

        // update workexperience
        const workExperienceOfUser: AngularFirestoreCollection<Account> = this.firestore.collection(
            this.dbWorkExperiencePath,
            ref => ref.where('username', '==', this.username)
        );
        workExperienceOfUser.get().subscribe((workExperienceDatas) => {
            account.workExperienceDTOs.forEach(workExperienceUpdate => {
                const workExperienceMapping = {
                    ...this.mappingService.mappingWorkExperienceToFirebase(workExperienceUpdate),
                    username: this.username
                }

                if (workExperienceUpdate.id) {
                    // update or delete
                    const docUpdate = workExperienceDatas.docs.find(doc => doc.id == workExperienceUpdate.id);

                    if (docUpdate) {
                        // update edu
                        promiseUpdateInfo.push(
                            this.firestore.doc(`${this.dbWorkExperiencePath}/${docUpdate.id}`).update(workExperienceMapping)
                        )
                    }
                } else {
                    // add new edu
                    promiseUpdateInfo.push(
                        this.firestore.collection(`${this.dbWorkExperiencePath}`).add(workExperienceMapping).then()
                    )
                }

            })

            const workExperienceIds = account.workExperienceDTOs.map(we => we.id)
            workExperienceDatas.docs.filter(doc => !workExperienceIds.includes(doc.id)).forEach(docDelete => {
                // delete
                promiseUpdateInfo.push(
                    this.firestore.doc(`${this.dbWorkExperiencePath}/${docDelete.id}`).delete()
                )
            })
        })

        // update skill
        const skillOfUser: AngularFirestoreCollection<Account> = this.firestore.collection(
            this.dbSkillPath,
            ref => ref.where('username', '==', this.username)
        );
        skillOfUser.get().subscribe((skillDatas) => {
            account.skillDTOs.forEach(skillUpdate => {
                const skillMapping = {
                    ...this.mappingService.mappingSkillToFirebase(skillUpdate),
                    username: this.username
                }

                if (skillUpdate.id) {
                    // update or delete
                    const docUpdate = skillDatas.docs.find(doc => doc.id == skillUpdate.id);

                    if (docUpdate) {
                        // update edu
                        promiseUpdateInfo.push(
                            this.firestore.doc(`${this.dbSkillPath}/${docUpdate.id}`).update(skillMapping)
                        )
                    }
                } else {
                    // add new edu
                    promiseUpdateInfo.push(
                        this.firestore.collection(`${this.dbSkillPath}`).add(skillMapping).then()
                    )
                }

            })

            const skillIds = account.skillDTOs.map(we => we.id)
            skillDatas.docs.filter(doc => !skillIds.includes(doc.id)).forEach(docDelete => {
                // delete
                promiseUpdateInfo.push(
                    this.firestore.doc(`${this.dbSkillPath}/${docDelete.id}`).delete()
                )
            })
        })

        return from(Promise.all(promiseUpdateInfo));
    }

    login(accountLogin: AccountLogin) {
        return from(
            this.fireauth.signInWithEmailAndPassword(accountLogin.email, accountLogin.password)
                .then((userCredential) => {
                    // Signed in 
                    const user = userCredential.user;

                    accountLogin.password = '';
                    this.currentAccountSubject.next(accountLogin);

                    return user;
                })
                .catch((error) => {
                    // login error
                })
        )

    }

    logout(): void {
        this.fireauth.signOut().then(() => {
            this.currentAccountSubject.next(null);
        });
    }

    private getAdditonalInformationOfUser<T>(dbPath: string): Observable<T[]> {
        const additonalOfUser: AngularFirestoreCollection<T> = this.firestore.collection(
            dbPath,
            ref => ref.where('username', '==', this.username)
                .orderBy('sortIndex', 'desc')
        );
        return additonalOfUser.snapshotChanges().pipe(
            map(changes =>
                changes.map(c => (
                    {
                        ...c.payload.doc.data(),
                        id: c.payload.doc.id
                    }
                ))
            )
        )
    }


}