import { Component, Input, OnChanges, OnInit } from '@angular/core';
import { async } from '@angular/core/testing';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { faMarkdown } from '@fortawesome/free-brands-svg-icons';
import { AccountLogin } from 'src/app/model/acccount-login';
import { Account } from 'src/app/model/account';
import { Post } from 'src/app/model/post';
import { AccountService } from 'src/app/service/account.service';
import { DateService } from 'src/app/service/date.service';
import { AccountFirebaseService } from 'src/app/service/firebase-cloud-firestore/account-firebase.service';
import { PostFirebaseService } from 'src/app/service/firebase-cloud-firestore/post-firebase.service';
import { PostService } from 'src/app/service/post.service';

@Component({
  selector: 'app-post-add',
  templateUrl: './post-add.component.html',
  styleUrls: ['./post-add.component.css']
})
export class PostAddComponent implements OnInit, OnChanges {
  @Input() postEditting?: Post;
  @Input() flagEdit: boolean;
  faMarkdown = faMarkdown;
  formPost: FormGroup;
  postPreview = '';
  currentAccount: AccountLogin;

  constructor(
    private fb: FormBuilder,
    // private postService: PostService,
    private postFirebaseService: PostFirebaseService,
    private dateService: DateService,
    private route: ActivatedRoute,
    private router: Router,
    // private accountService: AccountService
    private accountFirebaseService: AccountFirebaseService
  ) {
  }

  ngOnChanges(): void {
    this.currentAccount = this.accountFirebaseService.getCurrentAccountValue;
    this.formPost = this.fb.group({
      heading: [this.flagEdit ? this.postEditting.heading : '', [Validators.required]],
      subHeading: [this.flagEdit ? this.postEditting.subHeading : '', [Validators.required]],
      urlImage: [this.flagEdit ? this.postEditting.urlImage : '', [Validators.required]],
      content: [this.flagEdit ? this.postEditting.content : '', [Validators.required]],
    });
    // console.log(this.formPost);
    this.postPreview = this.formPost.get('content').value;
  }
  ngOnInit(): void {
    this.currentAccount = this.accountFirebaseService.getCurrentAccountValue;

  }

  submitPost(): void {
    if (!this.currentAccount) {
      return;
    }
    const post = new Post();
    post.heading = this.formPost.get('heading').value;
    post.subHeading = this.formPost.get('subHeading').value;
    post.content = this.formPost.get('content').value;
    post.urlImage = this.formPost.get('urlImage').value;

    const accountDTO = new Account();
    accountDTO.username = this.currentAccount.username;
    post.accountDTO = accountDTO;

    if (this.flagEdit) {
      post.id = this.postEditting.id;
      post.dateCreate = this.postEditting.dateCreate;
      this.updatePost(post);
    } else {
      post.dateCreate = this.dateService.getDayNow();
      console.log('INSERT', post);
      this.postFirebaseService.addPost(post).subscribe({
        next: (next) => {
          console.log(next);
        },
        error: (err) => {
          console.error(err);
        },
        complete: () => {
          console.log(post);
          this.router.navigateByUrl('/duongnh/home');
        }
      });
    }
  }
  updatePost(post: Post): void {
    this.postFirebaseService.updatePost(post).subscribe({
      next: (next) => {
        console.log('ADD POST: ' + next);
      },
      error: (err) => {
        console.error('ADD POST: ', err);
        // if (err.status === 401) {
        //   this.updatePost(post);
        // }
      },
      complete: () => {
        console.log(post);
        this.router.navigateByUrl(`/duongnh/post/detail/${this.postEditting.id}`);
      }
    });
  }
  deletePost(): void {
    if (!this.currentAccount) {
      return;
    }
    const idDelete: any = this.postEditting.id;
    this.postFirebaseService.deletePost(idDelete).subscribe(post => {
      console.log(post);
      this.router.navigateByUrl('/duongnh/home');
    });
  }
  resizeInput(element: HTMLTextAreaElement): void {
    element.style.height = 'auto';
    element.style.height = element.scrollHeight + 'px';
    this.postPreview = this.formPost.get('content').value;
  }

}
