import { Component, OnInit, OnChanges } from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import { Post } from '../../model/post';

import {PostService} from '../../service/post.service';
import { PostFirebaseService } from 'src/app/service/firebase-cloud-firestore/post-firebase.service';

@Component({
  selector: 'app-edit-post-page',
  templateUrl: './edit-post-page.component.html',
  styleUrls: ['./edit-post-page.component.css']
})
export class EditPostPageComponent implements OnInit {
  post: Post = new Post();
  constructor(
    private route: ActivatedRoute,
    private postFirebaseService: PostFirebaseService
    // private postService: PostService
  ) { }
  ngOnInit(): void {
    this.getPostEdit();
  }
  getPostEdit(): void{
    const id = this.route.snapshot.paramMap.get('id');
    this.postFirebaseService.getPostById(id).subscribe(responsePost => {
      if (responsePost) {
        this.post = responsePost;
        console.log('idPost:' + id, this.post);
      }
    });
  }
}
