import { Injectable, INJECTOR } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/auth';
import { AngularFirestore, AngularFirestoreCollection } from '@angular/fire/firestore';
import { BehaviorSubject, from, Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';
import { Post } from 'src/app/model/post';


@Injectable({
    providedIn: 'root'
})
export class PostFirebaseService {
    private username = "duongnh"
    private dbPostpath = "/posts"

    constructor(
        private firestore: AngularFirestore,
        private fireauth: AngularFireAuth
    ) {
        this.fireauth.onAuthStateChanged((user) => {
            if (user) {
                console.log(`user login ${user.displayName}`)
            } else {
            }
        })

        this.fireauth.credential.subscribe
    }

    getAllPost(): Observable<Post[]> {
        const posts: AngularFirestoreCollection<Post> = this.firestore.collection(
            this.dbPostpath,
            ref => ref.where('username', '==', this.username)
            .orderBy('dateCreate', 'desc')
        );

        return posts.snapshotChanges().pipe(
            map(changes =>
                changes.map(c => {
                    const post = {
                        ...c.payload.doc.data(),
                        id: c.payload.doc.id
                    }

                    return post;
                })
            )
        );
    }
    getPostById(id: string): Observable<Post> {
        const posts: AngularFirestoreCollection<Post> = this.firestore.collection(
            this.dbPostpath,
            ref => ref.where('username', '==', this.username)
                .where('id', '==', id)
        );

        return posts.snapshotChanges().pipe(
            map(changes =>
                changes.map(c => {
                    const post = {
                        ...c.payload.doc.data(),
                        id: c.payload.doc.id
                    }

                    return post;
                })[0]
            )
        );
    }

    addPost(post: Post): Observable<Post> {
        return from(
            this.firestore.collection(`${this.dbPostpath}`).add({
                ...post,
                dateCreate: post.dateCreate.toISOString(),
                accountDTO: null,
                username: this.username
            }).then(doc => {
                doc.update({ id: doc.id })
                return {
                    ...post,
                    id: doc.id
                }
            })
        )
    }

    updatePost(post: Post): Observable<Post> {
        return from(
            this.firestore.doc(`${this.dbPostpath}/${post.id}`).update({
                ...post,
                accountDTO: null
            }).then(_ => {
                return post
            })
        )
    }

    deletePost(id: string): Observable<Post> {
        return from(
            this.firestore.doc(`${this.dbPostpath}/${id}`).delete().then(_ => new Post())
        )
    }
}
