import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { Observable } from 'rxjs';
import { AccountService } from '../service/account.service';
import { AccountFirebaseService } from '../service/firebase-cloud-firestore/account-firebase.service';

@Injectable({ providedIn: 'root' })
export class AuthGuard implements CanActivate {

  constructor(
    private router: Router,
    // private accountService: AccountService
    private accountFirebaseService: AccountFirebaseService
  ) { }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean {
    const account = this.accountFirebaseService.getCurrentAccountValue;
    if (account) {

      return true;
    }
    this.router.navigateByUrl('/login');

    return false;
  }

}
