import { Component, OnInit } from '@angular/core';
import { Post } from 'src/app/model/post';
import { AccountFirebaseService } from 'src/app/service/firebase-cloud-firestore/account-firebase.service';
import { PostFirebaseService } from 'src/app/service/firebase-cloud-firestore/post-firebase.service';
import { PostService } from 'src/app/service/post.service';

declare interface Window {
  adsbygoogle: any[];
}
declare var adsbygoogle: any[];

@Component({
  selector: 'app-post-list',
  templateUrl: './post-list.component.html',
  styleUrls: ['./post-list.component.css']
})
export class PostListComponent implements OnInit {
  posts: Post[];
  constructor(
    // private postService: PostService,
    private postFirebaseService: PostFirebaseService,
  ) { }

  ngOnInit(): void {
    this.getAllPost();
  }

  ngAfterViewInit() {
    setTimeout(() => {
      try {
        (window["adsbygoogle"] = window["adsbygoogle"] || []).push({});
        (window["adsbygoogle"] = window["adsbygoogle"] || []).push({});
        (window["adsbygoogle"] = window["adsbygoogle"] || []).push({});
        (window["adsbygoogle"] = window["adsbygoogle"] || []).push({});
      } catch (e) {
        console.error("ads", e);
      }
    }, 900);
  }

  getAllPost(): void {
    // let arrPost: Array<Post> = null;
    this.postFirebaseService.getAllPost().subscribe(
      responsePost => {
        this.posts = responsePost;
        // this.posts = Array.from(this.posts).reverse();
      },
      error => {
        console.log(error);
        this.posts = [];
      });
  }
}
