import { Component, OnInit } from '@angular/core';
import { Account } from 'src/app/model/account';
import { AccountService } from 'src/app/service/account.service';
import { AccountFirebaseService } from 'src/app/service/firebase-cloud-firestore/account-firebase.service';

@Component({
  selector: 'app-amend-post',
  templateUrl: './amend-post.component.html',
  styleUrls: ['./amend-post.component.css']
})
export class AmendPostComponent implements OnInit {
  account: Account = new Account();

  constructor(
    // private accountService: AccountService
    private accountFirebaseService: AccountFirebaseService
  ) { }

  ngOnInit(): void {
    this.getLinkSocialNetworkById();
  }
  getLinkSocialNetworkById(): void {
    this.accountFirebaseService.getLinkSocialNetworkById().subscribe(
      (res) => {
        this.account = res;
      }
    );
  }
}
